<!--  -->
<template>
  <div>
    <div class="albums">
      <music-list :dataList="albumData.albumList" pageType="album" idPropName="albumid">
        <template v-slot:info="props">
          <p class="name">
            <!-- album中有空格的编码，需要使用v-html指令转换 -->
            <span :title="props.item.album" v-html="props.item.album" @click="goPage('album',props.item.albumid)"></span>
          </p>
          <p class="time">{{props.item.releaseDate}}</p>
        </template>
      </music-list>
      <music-no-data :keyWord="keyWord" v-show="albumData.code!=200 & albumData.code!=0"></music-no-data>
    </div>
    <pagination :code="albumData.code" :currentChange="albumData.getData" :pageSize="albumData.pageSize" :total="albumData.total"></pagination>

  </div>
</template>

<script>
import mixin from '../mixins';
import MusicList from "./MusicList.vue";
import MusicNoData from "./MusicNoData.vue";
import Pagination from "./Pagination.vue";
export default {
  name: "AlbumList",
  mixins: [mixin],
  props: {
    albumData: {
      type: Object,
      default: () => ({ code: 0 })
    },
    keyWord: String,
  },
  data () {
    return {
    };
  },

  components: {
    MusicNoData,
    MusicList,
    Pagination
  },

  methods: {},

  computed: {},

  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang='scss' scoped>
.albums {
  .name {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    margin-top: 10px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    display: block !important;
    span {
      cursor: pointer;
    }
  }
  .time {
    color: #999;
    font-weight: 300;
    margin-top: 5px;
    font-size: 14px;
    line-height: 1;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
      sans-serif;
  }
}
</style>
<style lang="scss">
.albums {
  .item {
    .cover_out {
      padding-bottom: 100%;
    }
  }
}
</style>



