<!--  -->
<template>
  <div class="main">
    <div class="bg">
      <div class="singer_info flex_c">
        <div class="cover_out">
          <img class="pic" :src="singerInfo.pic300" />
        </div>
        <div class="info">
          <span class="singer_name">{{singerInfo.name}}</span>
          <div class="info_items">
            <span>
              单曲：<span>{{singerInfo.musicNum||'-'}}</span>
            </span>
            <span>
              专辑：<span>{{singerInfo.albumNum||'-'}}</span>
            </span>
            <span>
              MV：<span>{{singerInfo.mvNum||'-'}}</span>
            </span>
            <span>
              粉丝：<span>{{singerInfo.artistFans|fansCnt}}</span>
            </span>
          </div>
          <div class="singer_items flex_c">
            <span>
              <span>
                英文名：<span v-html="singerInfo.aartist||'-'"></span>
              </span>
              <span>
                国籍：<span>{{singerInfo.country||'-'}}</span>
              </span>
              <span>
                语言：<span>{{singerInfo.language||'-'}}</span>
              </span>
              <span>
                出生地：<span>{{singerInfo.birthplace||'-'}}...</span>
              </span>
            </span>
            <span class="all" @click="isActive=3">全部 ></span>
          </div>
          <!-- <div class="btns">

        </div> -->
        </div>
      </div>
    </div>
    <!-- 歌曲、专辑、mv、简介 -->
    <div class="main_con">
      <div class="tabs flex_c">
        <template v-for="item in tabs">
          <span :class="{active:isActive===item.id}" :key="item.id" @click="isActive=item.id;getData()">{{item.name}}</span>
        </template>
      </div>
      <div class="child_view">
        <!-- 单曲 -->
        <div v-if="isActive===0">
          <div class="songs">
            <music-table :pageNum="pageNum" :songList="songList" v-show="code == 200"></music-table>
            <music-no-data v-show="(code != 200) & (code != 0)"></music-no-data>
          </div>
          <pagination :code="code" :currentChange="getMusicList" :pageSize="30" :total="total"></pagination>
        </div>
        <!-- 专辑 -->
        <div v-else-if="isActive===1">
          <album-list :albumData="albumData"></album-list>
        </div>
        <!-- Mv -->
        <div v-else-if="isActive===2">
          <mv-list :mvData="mvData"></mv-list>
        </div>
        <!-- 简介 -->
        <div v-else>
          <p class="tit">基本信息</p>
          <ul class="info_list">
            <li class="flex_c">
              <div class="item_l">
                <span>姓名：<span>{{singerInfo.name}}</span></span>
              </div>
              <div class="item_r">
                <span>英文名：<span v-html="singerInfo.aartist||'-'"></span></span>
              </div>
            </li>
            <li class="flex_c">
              <div class="item_l">
                <span>性别：<span>{{singerInfo.gener||'-'}}</span></span>
              </div>
              <div class="item_r">
                <span>国籍：<span>{{singerInfo.country||'-'}}</span></span>
              </div>
            </li>
            <li class="flex_c">
              <div class="item_l">
                <span>出生地：<span>{{singerInfo.birthplace||'-'}}</span></span>
              </div>
              <div class="item_r">
                <span>语言：<span>{{singerInfo.language||'-'}}</span></span>
              </div>
            </li>
            <li class="flex_c">
              <div class="item_l">
                <span>生日：<span>{{singerInfo.birthday||'-'}}</span></span>
              </div>
              <div class="item_r">
                <span>星座：<span>{{singerInfo.constellation||'-'}}</span></span>
              </div>
            </li>
            <li class="flex_c">
              <div class="item_l">
                <span>身高：<span>{{singerInfo.tall||'-'}}</span></span>
              </div>
              <div class="item_r">
                <span>体重：<span>{{singerInfo.weight||'-'}}</span></span>
              </div>
            </li>
          </ul>

          <p class="tit">个人简介</p>
          <p v-html="'&nbsp;'+singerInfo.info||'-'" class="info"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { HttpManager } from '../../api';
import MvList from '../../components/MvList.vue';
import AlbumList from '../../components/AlbumList.vue'
import MusicNoData from '../../components/MusicNoData.vue';
import MusicTable from '../../components/MusicTable.vue';
import Pagination from '../../components/Pagination.vue';

export default {
  name: "SingerDetail",
  data () {
    return {
      singerInfo: {},
      tabs: [
        { name: '单曲', id: 0 },
        { name: '专辑', id: 1 },
        { name: 'MV', id: 2 },
        { name: '简介', id: 3 },
      ],
      isActive: 0,
      songList: [],
      albumData: {},
      mvData: {},
      pageNum: 0,
      code: 0,
      total: 0,

    };
  },

  components: {
    AlbumList,
    MusicTable,
    MusicNoData,
    MvList,
    Pagination
  },

  methods: {
    getSingerInfo () {
      let that = this;
      HttpManager.getSingerInfo(this.$route.params.id)
        .then((res) => {
          that.singerInfo = res.data;
        })
    },
    getMusicList (val) {
      let that = this;
      this.pageNum = val;
      HttpManager.getSingerMusic({ artistid: this.$route.params.id, pn: val, rn: 30 })
        .then((res) => {
          that.songList = res.data.list;
          that.code = res.code;
          that.total = parseInt(res.data.total);
        })
    },
    getAlbumList (val) {
      let that = this;
      HttpManager.getSingerAlbum({ artistid: this.$route.params.id, pn: val, rn: 30 })
        .then((res) => {
          that.albumData = Object.assign({}, that.albumData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              albumList: res.data.albumList,
              pageSize: 30,
              getData: that.getAlbumList,
            })
          // that.albumList = res.data.albumList;
          // that.code = res.code;
          // that.total = parseInt(res.data.total);
        })
    },
    getMvList (val) {
      let that = this;
      HttpManager.getSingerMv({ artistid: this.$route.params.id, pn: val, rn: 30 })
        .then((res) => {
          that.mvData = Object.assign({}, that.mvData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              mvList: res.data.mvlist,
              pageSize: 30,
              getData: that.getMvList,
            })
          // that.mvList = res.data.mvlist;
          // that.code = res.code;
          // that.total = parseInt(res.data.total);
        })
    },
    getData () {
      //自动回顶部
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      switch (this.isActive) {
        case 0: this.getMusicList(1); break;
        case 1: this.getAlbumList(1); break;
        case 2: this.getMvList(1); break;
        default: break;
      }
    },
  },

  computed: {},
  filters: {
    fansCnt (val) {
      let num = parseInt(val);
      if (num < 10000) {
        return num;
      } else {
        return (num / 10000).toFixed(1) + "万"; //取小数点后一位
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getSingerInfo();
    this.getMusicList(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.bg {
  background-color: #f5f5f5;
  position: relative;
  top: -20px;
  left: -120px;
  width: 100vw;
  min-width: 990px;
}
.singer_info {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  padding: 20px 120px 20px;
  .cover_out {
    max-height: 540px;
    overflow: auto;
    position: relative;

    width: 20%;
    // margin: 0 20px 20px 0;
    .pic {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  .info {
    position: relative;
    width: 76.14%;
    z-index: 10;
    margin-left: 20px;
    padding: 0;
    .singer_name {
      vertical-align: baseline;
      display: inline-block;
      // margin: 10px 0 0;
      height: 30px;
      line-height: 30px;
      font-size: 28px;
      font-weight: 600;
    }
    .artist {
      font-size: 18px;
      height: 25px;
      color: #666;
      line-height: 25px;
      font-weight: 400;
      margin-top: 15px;
    }
    .info_items {
      color: #999;
      margin-top: 15px;
      font-size: 16px;
      font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
        sans-serif;
      span span {
        margin-right: 30px;
        color: #666;
      }
    }
    .singer_items {
      color: #666;
      margin-top: 15px;
      font-size: 14px;
      font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
        sans-serif;
      span {
        margin-right: 5px;
      }
      .all {
        cursor: pointer;
        color: #296294;
      }
    }
    // .btns {
    //   margin: 25px 0 20px;
    //   .play {
    //     width: 130px;
    //     background: #31c27c;
    //     color: #fff;
    //     border: none;
    //     &:hover {
    //       color: #000;
    //     }
    //     &:active {
    //       box-shadow: 0 0 10px 0 rgb(65 67 70 / 30%) inset;
    //       border: none;
    //     }
    //   }
    // }
    // .list_out {
    //   box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
    // }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
.main_con {
  padding-top: 20px;
  font-size: 14px;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  .tabs {
    align-items: baseline;
    margin: 10px 0;
    font-size: 18px;
    span {
      position: relative;
      height: 20px;
      line-height: 20px;
      font-weight: 300;
      color: #666;
      margin-right: 30px;
      &.active {
        font-weight: 600;
        color: #000;
        &:after {
          position: absolute;
          content: "";
          left: 0;
          bottom: 1px;
          width: 100%;
          height: 6px;
          background: #31c27c;
          z-index: -1;
        }
      }
      &:hover {
        color: #000;
      }
    }
  }
  .child_view {
    margin-top: 40px;
    .tit {
      position: relative;
      margin-bottom: 20px;
      margin-top: 30px;
      height: 25px;
      line-height: 25px;
      font-weight: 600;
      padding-left: 14px;
      font-size: 18px;
      &:before {
        position: absolute;
        left: 0;
        top: 3px;
        content: "";
        height: 76%;
        width: 4px;
        background: #31c27c;
      }
    }
    .info_list {
      li {
        &:nth-child(2n-1) {
          background: #f8f8f8;
        }
        line-height: 50px;
        height: 50px;
        padding-left: 30px;
        .item_l,
        .item_r {
          width: 50%;
          span {
            color: #999;
            span {
              color: #000;
            }
          }
        }
      }
    }
    .info {
      line-height: 25px;
      font-weight: 300;
    }
  }
}
</style>
<style lang="scss">
.albums {
  .item {
    .cover_out {
      padding-bottom: 100%;
    }
  }
}
.mvs {
  .item {
    .cover_out {
      padding-bottom: 59% !important;
    }
  }
}
</style>



