<!--  -->
<template>
  <div>

    <div class="mvs">
      <music-list :dataList="mvData.mvList" pageType="mvPlay" idPropName="id">
        <template v-slot:play_cnt="props">
          <p class="play_cnt">
            <span>
              <span>
                <i class="iconfont icon-icon_play_"></i>
                {{props.item.mvPlayCnt|playCnt}}
              </span>
            </span>
            <span>{{props.item.songTimeMinutes}}</span>
          </p>
          <div class="shadow"></div>
        </template>
        <template v-slot:info="props">
          <p class="name">
            <span :title="props.item.name" @click="goPage('mvPlay',props.item.id)" v-html="props.item.name"></span>
          </p>
          <p class="artist">
            <span :title="props.item.artist">{{props.item.artist}}</span>
          </p>
        </template>
      </music-list>

      <music-no-data :keyWord="keyWord" v-show="mvData.code!=200 & mvData.code!=0"></music-no-data>
    </div>
    <pagination :code="mvData.code" :currentChange="mvData.getData" :pageSize="mvData.pageSize" :total="mvData.total"></pagination>
  </div>
</template>

<script>
import mixin from '../mixins';
import MusicList from "./MusicList.vue";
import MusicNoData from "./MusicNoData.vue";
import Pagination from "./Pagination.vue";
export default {
  name: "MvList",
  mixins: [mixin],
  props: {
    mvData: {
      type: Object,
      default: () => ({ code: 0 })
    },
    keyWord: String,
  },
  data () {
    return {
    };
  },

  components: {
    MusicNoData,
    MusicList,
    Pagination
  },

  methods: {},

  computed: {},
  filters: {
    playCnt (val) {
      let num = parseInt(val);
      if (num < 10000) {
        return num;
      } else {
        return (num / 10000).toFixed(1) + "万"; //取小数点后一位
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
.mvs {
  .play_cnt {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 3px 5px;
    background-color: rgba(2, 2, 2, 0.3);
    color: #fff;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    z-index: 10;
  }
  .name {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    margin-top: 10px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    display: block !important;
    span {
      cursor: pointer;
    }
  }
  .artist {
    color: #999;
    font-weight: 300;
    margin-top: 5px;
    font-size: 14px;
    line-height: 1;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
      sans-serif;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
</style>
<style lang="scss">
.mvs {
  .item {
    .cover_out {
      padding-bottom: 59% !important;
    }
  }
}
</style>



